import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, LoadingController, NavOptions, AlertController, ToastController, MenuController, NavParams } from 'ionic-angular';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';

//PAGE COMMON IMPORTS
import { NewModalPage } from '../new-modal/new-modal';
import { GlobalsProvider } from '../../providers/globals/globals';
import { UserProvider } from '../../providers/user/user';
import { ModalController } from 'ionic-angular';

@IonicPage({
  name: 'reset-password',
  segment: 'ResetPassword',
  priority: 'high'
})

@Component({
  selector: 'reset-password',
  templateUrl: 'reset-password.html'
})

export class ResetPassword implements OnInit {
  auth: string = "register";
  tags: string;
  tag: string;
  tags1 = [];
  tags2 = [];
  arr = [];
  newWord: string;
  str: string;
  disable: boolean = true;
  disable1: boolean = false;
  counter: number = 0;
  backspace: boolean = false;
  show: boolean = false;
  email: any;
  password: any;
  country: any;
  newScopeVariable: number = 0;
  modalDismissData: any;
  step: number = 0;
  validAddy: boolean = false;
  
  //PAGE COMMING VARS
  globals: any = null;

  constructor(public loadingCtrl: LoadingController, public userService: UserProvider,
    public translate: TranslateService, public navparams: NavParams, public nav: NavController,
    public forgotCtrl: AlertController, public menu: MenuController, public toastCtrl: ToastController,
    public modalCtrl: ModalController, globals: GlobalsProvider) {
    this.translate.setDefaultLang('en');
    this.translate.use('es');
    this.menu.swipeEnable(false);
    this.menu.enable(false);

    //PAGE COMMON INITIATION:
    //public modalCtrl: ModalController, globals: GlobalsProvider
    this.globals = globals;
  }

  validateEmailAddy() {
    let re = /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
    let e = this.email;
    if (!e) return false;

    this.validAddy = re.test(e.toLowerCase());
    return this.validAddy;
  }

  showNext(direction: number) {
    if (!this.validateEmailAddy()) return false;

    this.step += direction;
    if (this.step > 1) {
      this.step = 1;
    }
    if (this.step < 1) {
      this.step = 0;
    }
  }

  // check regular expression
  updateList(event) {
    if (this.newWord === null || this.newWord == undefined) {
      return;
    }

    this.newWord = this.newWord.trim();
    //protecting newValue null pointer


    let regExp = new RegExp(/^[a-zA-Z0-9]{1,19}$/);

    if (!regExp.test(this.newWord)) {

      this.translate.get('resgister-pag-2.error_length').subscribe((translated: string) => {
        var toast1 = this.translate.instant('Invalid word entered.');
        let toast = this.toastCtrl.create({
          message: toast1,
          duration: 3000,
          position: 'top'

        });
        toast.present();
      });

      this.newWord = this.newWord.slice(0, -1);
      return false;
    }

    if (this.arr.length == 12) {

      this.translate.get('resgister-pag-2.error_count').subscribe((translated: string) => {
        var toast1 = this.translate.instant('resgister-pag-2.error_count');
        let toast = this.toastCtrl.create({
          message: toast1,
          duration: 3000,
          position: 'top'

        });
        toast.present();
      });


      return false;
    }

    //add item to array once space is pressed
    regExp = new RegExp(/[`~!@#$%^&*()_|+\-=?;:",.<>\s\{\}\[\]\\\/\-]/gi);
    let outString = this.newWord.replace(regExp, '');

    // It has any kind of whitespace
    this.arr.push(outString);

    this.newWord = null;

    return;
  }

  checkEnter(event: any) {
    if (this.a11yClick(event)) {
      this.updateList(event);
    }
  }


  a11yClick(event: any) {

    var code: any = event.charCode || event.keyCode;
    code = event.charCode || event.keyCode;
    if ((code === 32) || (code === 13)) {
      return true;
    }
  }


  ngOnInit() {

  }

  


  showLogin(fab) {
    fab.close();
    this.nav.setRoot('page-auth');
  }

  //returns a piped string of seed words
  makePipedList(): string | boolean {
    if (!this.arr) return false;

    let seedstring: string = "";

    this.arr.forEach((word, index) => {
      seedstring += word;
      if (index != this.arr.length - 1) {
        seedstring += "|";
      }
    });
    console.log(seedstring);
    return seedstring;
  }

  //delete incorrect tag
  removeTag(tagId) {

    this.arr.splice(tagId, 1);
    return;
  }


  //PAGE COMMON FUNCTIONS

  showHelp(fab: any) {
    fab.close();
    this.openModal();
  }

  //opens communication modal
  openModal() {
    var msg: string = "reset-password-page.help-text";

    this.translate.get(msg).subscribe(async (translated: string) => {
      var content = this.translate.instant(translated);
      const helpModal = this.modalCtrl.create(NewModalPage, { html: content });
      await helpModal.present();
    });
  }

  //checks if string is JSON
  isJson(str: any) {
    try {
      JSON.parse(str);
    } catch (e) {
      return false;
    }
    return true;
  }

  // check tags and go to next tag page
  sendRequest() {


    this.translate.get('Auth-page').subscribe((translated: string) => {
      var loader = this.translate.instant('Auth-page.loader');
      let loading = this.loadingCtrl.create({ content: loader });
      loading.present();

      let seed = this.makePipedList();

      if (!seed) { return false };

      this.userService.doPasswordReset(this.email, seed).subscribe((data: any) => {

        if (!data._body) {
          console.log("Body returned null. Server error.");
          return false;
        }

        if (!this.isJson(data._body)) {
          console.log("Body cannot be parse, not JSON.");
          return false;
        }

        let response = JSON.parse(data._body);

        if (!response) {
          console.log("Failed to convert body");
          return false;
        }

        let body: any = response.data;

        if (!response.success) {
          //throw network error toast
          console.log("network error: success = false");
          this.userService.setToast(response.msg);
          return;
        }

        let str: string = "Please contact support to reset your password: support@azuzawealth.com";

        if (body) {

          this.userService.setToast(body.msg ? body.msg : str);

        } else {

          let response = this.userService.setToast(str);

        }

      }, error => {
        this.userService.setToast("A network error occurred. Please try again or contact support: support@azuzawealth.com")

      },
        () => {
          loading.dismiss();
          setTimeout(() => {
            this.nav.setRoot('page-auth');
          }, 3000);
        }
      )



    })
  }

  


}







